import React, { Component } from 'react'
import '../css/home.css'
import {BrowserRouter as Router,Route,Link,Switch} from 'react-router-dom'
import All from './All'
import Good from './Good'
import ASK from './ASK'
import Job from './Job'
import Share from './Share'
import $ from 'jquery'

export default class Home extends Component {
    click=()=>{
        $('#data-ul').css('display','block');
        $('#tab-tab').css('display','block');
        $('#data-content').css('display','none');
    }
    render() {
        return (
            <div>
                <Router>
                    <div id="home-tab">
                        <Link to='/all' onClick={this.click}>全部</Link>
                        <Link to="/good" onClick={this.click}>精华</Link>
                        <Link to="/share" onClick={this.click}>分享</Link>
                        <Link to="/ask" onClick={this.click}>问答</Link>
                        <Link to='/job' onClick={this.click}>招聘</Link>
                    </div>
                    <div>
                        <Switch>
                            <Route exact path="/" component ={All} />
                            <Route path="/home" component ={All} />
                            <Route path="/all" component ={All} />
                            <Route path='/good' component={Good} />
                            <Route path='/share' component={Share} />
                            <Route path='/ask' component={ASK} />
                            <Route path='/job' component={Job} />
                        </Switch>
                    </div>
                </Router>
            </div>
        )
    }
}
