import React, { Component } from 'react'
import './css/index.css'
import {BrowserRouter as Router,Route,Link,Switch,Redirect} from 'react-router-dom'
import API from './About/API'
import About from './About/About'
import Start from './About/Start'
import Home from './Home/Home'
import Login from './Login'
import All from './Home/All'

export default class Index extends Component {
    render() {
        return (
            <Router>
            <div>
                <div id="top">
                   <div className="container">
                        <img src="//static2.cnodejs.org/public/images/cnodejs_light.svg" id='img-top' />
                        <input type='text' id="top-inp"  />
                        <ul id="top-ul">
                            <li><Link to="/login">登录</Link></li>  
                            <li><Link to='/about'>关于</Link></li>
                            <li><Link to="/api">API</Link></li>
                            <li><Link to="/start">新手入门</Link></li>
                            <li><Link to="/home">首页</Link></li>                      
                        </ul> 
                   </div>
                </div>
                {/* main */}
                <div id="main">
                    <div className="container">
                        <div id="main-left">
                            <Switch>
                                <Route exact path='/' component={Home} />
                                <Route path='/about' component={About} />
                                <Route path='/home' component={Home} />
                                <Route path='/login' component={Login} />
                                <Route path='/start' component={Start} />
                                <Route path='/api' component={API} />
                            </Switch>
                        </div>
                        <div id="main-right">
                            <div id='m-r'>
                                客户端二维码
                            </div>
                            <img width="200" src="//static.cnodejs.org/FtG0YVgQ6iginiLpf9W4_ShjiLfU" id="r-img" />
                        </div>
                    </div>
                </div>
            </div>
            </Router>
        )
    }
}
